<template>
  <div>
    <user-nav-bar/>
    <h2>add new user</h2>
    <user-form @createOrUpdate="createOrUpdate"></user-form>
  </div>
</template>

<script>
import userNavBar from '@/components/Users/UserNavBar.vue'
import userForm from '@/components/Users/UserForm.vue'
import { api } from '@/helpers/Helpers'

export default {
  name: 'new-user',
  components: {
    'user-form': userForm,
    'user-nav-bar': userNavBar
  },
  methods: {
    createOrUpdate: async function (user) {
      const res = await api.createuser(user)
      this.flash('user created', 'success')
      this.$router.push(`/users/${res._id}`)
    }
  }
};
</script>