<template>
 <form class="form-signin">
    <img class="mb-4" src="http://www.hexsoftstudio.com/training/wp-content/uploads/2020/03/HS-logo2020.5-90x90.png" alt="" width="72" height="72">
    
    <h1 class="h4 mb-4 font-weight-normal">Please {{ title }}</h1>
    
    <label for="inputEmail" class="sr-only">Email address</label>
    <input type="email" id="inputEmail" class="form-control" placeholder="Email address" required autofocus>
    
    <label for="inputPassword" class="sr-only">Password</label>
    <input type="password" id="inputPassword" class="form-control" placeholder="Password" required>
    
    <div v-if="isDspRememberMe" class="checkbox mb-3">
      <label>
        <input type="checkbox" value="remember-me"> Remember me
      </label>
    </div>

    <button class="btn btn-lg btn-primary btn-block" type="submit">{{ title }}</button>
    <p class="mt-5 mb-3 text-muted">Hexsoft Studio &copy; 2020</p>
  </form>
</template>

<script>
export default {
  name: 'sign-in-form',
  props: {
    title: String,
    isDspRememberMe: {
      type: Boolean,
      required: false,
      default: false
    }
  },
  data() {
    return {
      user: {
        email: '',
        password: ''
      },
      errorsPresent: false
    };
  },
  methods: {
    onSubmit: function() {
      if (this.user.email === '' || this.user.password === '') {
        this.errorsPresent = true;
      } else {
        this.$emit('createOrUpdate', this.user);
      }
    }
  }
};
</script>

<style src="./signin.css" scoped>
.error {
  color: red;
}
</style>
